}
.comman-header  h2.logo-img{
    color: #fff;
    padding: 0px 20px;
    font-family: 'ZCOOL XiaoWei', serif;
}

.vertical-align-outer-div {
    display: table;
    height: 100vh;
    width: 100%;
    overflow: hidden;
}
.vertical-align-inner-div {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}

.overlay
{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #eeeeee45;
    z-index: 2;
    cursor: pointer;
    overflow-y: scroll;
}
.overlay .modal
{
    width: 94%;
    margin: 80px auto;
    background-color: #ffffff;
    border-radius: 4px;
}
.overlay .modal h3
{
    margin-top: 7px;
    margin-bottom: 0px;
}
.overlay .modal .modal-header
{
    padding: 15px 20px;
    border-bottom: 1px solid #f5f5f5;
}
.overlay .modal .modal-body
{
    padding: 20px;
    border-bottom: 1px solid #f5f5f5;
}
.overlay .modal .template-img
{
    position: relative; 
    width: 100%;
    height: 250px;
    margin: 0 auto;
}
.overlay .modal img
{
    position: absolute;
    width: 100%;
    height: 100%;
}
.overlay .modal .template-img .overlay-div
{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.20);
    display: none;
}
.overlay .modal .modal-footer
{
    padding: 20px;
}
.overlay .modal .vertical-align-outer-div
{
    display: table;
    height: 585px;
    width: 100%;
    overflow: hidden;
}
.overlay .modal .vertical-align-inner-div
{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}
.overlay .modal .overlay-div .MuiSvgIcon-root
{
    color: #ffffff;
    font-size: 40px;
}
.overlay .modal .active .overlay-div
{
    display: block;
}
