ul.option-list
{
	padding-inline-start: 0px;
}
ul.option-list li 
{
	display: inline-block;
	padding: 8px 15px;
	border: 1px solid #eeeeee;
	margin: 4px;
	border-radius: 18px;
}
ul.option-list a
{
	color: #000000;
}
ul.option-list li.active, ul.option-list li:hover 
{
	border: 1px solid #FF9800;
	color: #FF9800;
}
ul.option-list li.active, ul.option-list li:hover
{
	color: #FF9800 !important;
}
ul.option-list li .MuiSvgIcon-root
{
    display: none;
}
ul.option-list li.active .MuiSvgIcon-root
{
	display: inline-block;
}
.res-content-area h2 .MuiBadge-anchorOriginTopRightRectangle
