*::-webkit-scrollbar {
  width: 7px;
}
*::-webkit-scrollbar-track {
	background: #e4e6e8;
}
*::-webkit-scrollbar-thumb {
	background-image: linear-gradient(#529c8b,#218ce8);
	border-radius: 25px;
}
*::-webkit-scrollbar-thumb:hover {
	background-image: linear-gradient(#7decd3,#e2b676);
}